.overlay{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5); 
        z-index: 9999; 
        font-family: Poppins;
        .add-member-popup{
            width: 574px;
            box-sizing: border-box;
            background-color: white;
            border:#ccc 1px solid ;
            border-radius: 5px;
            padding:10px 20px;
            position: absolute;
            top:30%;
            left:50%;
            z-index: 15;
            transform:translate(-50%);
            z-index: 100000;
            font-family: Poppins;
