.added-members-input{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 5px;
    border:#ccc 1px solid ;
    border-radius: 5px;
    padding:2px 2px;

    >div{
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 5px;
        cursor: pointer;
        font-size: small;
        border: #ccc 1px solid ;
        border-radius: 5px;
        height: 20px;
        padding: 0 5px;
        min-width: 50px;
    }
    >input{
        width: 150px;
        border: none;

    }
}

            .close-btn{
                position: fixed;
                right:-40px;
                top:0;
                background-color: transparent;
                width: 20px;
                color: #000;
                font-weight: 600;
                transform: translateX(10px);
                cursor: pointer;
            }
            >label{
                display: block;
                margin-top: 10px;
                color: #000;
                font-size: 15px;
                font-family: Poppins;
                font-style: normal;
                font-weight: 500;
                line-height: 140%;
                font-family: Poppins;
            }
            input{
        
                width: 100%;
                border:#efefef 1px solid ;
                border-radius: 5px;
                outline:none;
                padding:5px 10px;
                max-height: 75px;
                font-family: Poppins;
              }
            .members{
                gap: 10px;
                font-family: Poppins;
                max-height: 100px;
                overflow-y: auto;
                // Define the styles for the scroll bar
                ::-webkit-scrollbar {
                width: 8px;  // Width of the scroll bar
  }
  
  // Track (background) of the scroll bar
  ::-webkit-scrollbar-track {
    background-color: #f1f1f1;  // Background color of the track
