                .single-member{
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    cursor: pointer;
                }
            }
            button{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background-color: #005734;
                    border: none;
                    outline: none;
                    color: white;
                    height: 30px;
                    width: 120px;
                    border-radius: 6px;
                    margin-left: auto;
                    margin-right: 10%;
                    cursor: pointer;
                    font-family: Poppins;
                    div{
                        margin: auto;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        gap: 5px;
                        font-weight: 600;
                        width: 155px;
                        font-family: Poppins;
                    }
            }
            .edit-team{
                margin-top: 10px;
                margin-right: 0;
            }
